import numberBox from '../../components/my-components/Number-Box'
export default {
    components: {
        numberBox
    },
    props: {
        info: {
            type: Object,
            default: {
                name: '标题',
                num: 0
            }
        }
    },
    methods: {
        change(val) {
            this.$emit('change', Object.assign({}, this.info, {
                newNumber: val[0]
            }))
        }
    },
}
